.App{
  width: 100%;
  height: 100vh;
  color: #fff;
  text-align: center;
}
.card{
  width: 100%;
  height: 100%;
  background-color: blue;
  color: #fff;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
li{
  border: 1px solid;
  text-align: center;
  width: 17em;
  height: 4em;
  margin: 5px;
  font-size: 17px;
  border-radius: 4px;
}
li:hover{
  background-color: rgb(33, 33, 236);
}